import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Stack,
  Button,
  ToggleButtonGroup,
  ToggleButton,
  CircularProgress,
  IconButton,
  Chip,
} from '@mui/material';
import { Share as ShareIcon } from '@mui/icons-material';
import { Compatibility as ICompatibility } from '../types/saju';
import { Friend } from '../types/friend';

// Mock data
const mockFriend: Friend = {
  id: '2',
  email: 'jane@example.com',
  name: 'Jane Smith',
  sajuReading: {
    id: '2',
    userId: '2',
    mainElement: 'Water',
    supportingElement: 'Metal',
    challengingElement: 'Fire',
    elementalBalance: {
      fire: 10,
      earth: 15,
      metal: 30,
      water: 35,
      wood: 10,
    },
    summary: 'Water dominant personality with Metal support.',
    detailedReading: 'Detailed reading...',
    createdAt: new Date().toISOString(),
  },
};

const mockCompatibility: ICompatibility = {
  id: '1',
  user1Id: '1',
  user2Id: '2',
  compatibilityScore: 75,
  relationship: 'Love',
  description: `Your Fire and their Water create an interesting dynamic. While there might be initial challenges, this combination can lead to growth and balance.

Key Points:
- Your passionate nature complements their calm demeanor
- Their wisdom can help temper your intensity
- Together, you can achieve great emotional depth

Recommendations:
1. Focus on open communication
2. Respect each other's different approaches
3. Use your differences as strengths
4. Find balance in decision-making`,
  createdAt: new Date().toISOString(),
};

const Compatibility: React.FC = () => {
  const { friendId } = useParams<{ friendId: string }>();
  const navigate = useNavigate();
  const [relationship, setRelationship] = useState<'Love' | 'Friendship' | 'Work'>('Love');
  const [isLoading, setIsLoading] = useState(false);

  const handleRelationshipChange = (
    _: React.MouseEvent<HTMLElement>,
    newRelationship: 'Love' | 'Friendship' | 'Work'
  ) => {
    if (newRelationship !== null) {
      setRelationship(newRelationship);
      setIsLoading(true);
      // TODO: Fetch new compatibility reading
      setTimeout(() => setIsLoading(false), 1000);
    }
  };

  const handleShare = () => {
    // TODO: Implement sharing functionality
    console.log('Share compatibility');
  };

  return (
    <Container 
      sx={{ 
        pb: '72px', // bottom navigation height (56px) + extra padding
        pt: 2,
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column'
      }}
    >
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h1" gutterBottom>
          Compatibility
        </Typography>
        <IconButton onClick={handleShare} color="primary">
          <ShareIcon />
        </IconButton>
      </Box>

      <Stack spacing={3} sx={{ flex: 1 }}>
        <Card>
          <CardContent>
            <Typography variant="h2" gutterBottom>
              Check Compatibility With
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
              <Typography variant="body1">
                {mockFriend.name}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                ({mockFriend.email})
              </Typography>
            </Box>
            <Box sx={{ mb: 3 }}>
              <ToggleButtonGroup
                value={relationship}
                exclusive
                onChange={handleRelationshipChange}
                fullWidth
              >
                <ToggleButton value="Love">Love</ToggleButton>
                <ToggleButton value="Friendship">Friendship</ToggleButton>
                <ToggleButton value="Work">Work</ToggleButton>
              </ToggleButtonGroup>
            </Box>
          </CardContent>
        </Card>

        {isLoading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
            <CircularProgress />
          </Box>
        ) : (
          <>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 3 }}>
                  <Typography variant="h2">
                    Compatibility Score
                  </Typography>
                  <Chip
                    label={`${mockCompatibility.compatibilityScore}%`}
                    color={
                      mockCompatibility.compatibilityScore >= 70 ? 'success' :
                      mockCompatibility.compatibilityScore >= 40 ? 'warning' : 'error'
                    }
                  />
                </Box>
                <Typography
                  variant="body1"
                  component="div"
                  sx={{ whiteSpace: 'pre-line' }}
                >
                  {mockCompatibility.description}
                </Typography>
              </CardContent>
            </Card>

            <Box sx={{ display: 'flex', gap: 2 }}>
              <Button
                variant="contained"
                color="primary"
                fullWidth
                onClick={handleShare}
              >
                Share Result
              </Button>
              <Button
                variant="outlined"
                color="primary"
                fullWidth
                onClick={() => window.print()}
              >
                Save as PDF
              </Button>
            </Box>
          </>
        )}
      </Stack>
    </Container>
  );
};

export default Compatibility; 