import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Stack,
  Switch,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  FormControlLabel,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  IconButton,
} from '@mui/material';
import { UserInfo, Gender } from '../types/user';
import { ChevronRight, ArrowBackIos } from '@mui/icons-material';

const Settings: React.FC = () => {
  const navigate = useNavigate();
  const [showEditDialog, setShowEditDialog] = useState(false);
  const [showLogoutDialog, setShowLogoutDialog] = useState(false);
  const [userInfo, setUserInfo] = useState<UserInfo | null>(null);

  useEffect(() => {
    const storedUserInfo = localStorage.getItem('userInfo');
    if (storedUserInfo) {
      setUserInfo(JSON.parse(storedUserInfo));
    }
  }, []);

  const handleEditSubmit = () => {
    if (userInfo) {
      localStorage.setItem('userInfo', JSON.stringify(userInfo));
      setShowEditDialog(false);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('userInfo');
    navigate('/');
  };

  const handleDeleteAllInformation = () => {
    localStorage.clear();
    navigate('/');
  };

  const handleFeatureRequest = () => {
    window.open('https://forms.google.com/feature-request', '_blank');
  };

  return (
    <Box sx={{ 
      minHeight: '100vh',
      pb: '80px'
    }}>
      {/* Header */}
      <Box sx={{ 
        position: 'sticky',
        top: 0,
        zIndex: 10,
        bgcolor: 'white',
        boxShadow: '0 1px 2px rgba(0, 0, 0, 0.05)',
      }}>
        <Box sx={{ 
          display: 'flex',
          alignItems: 'center',
          p: 2,
        }}>
          <IconButton 
            onClick={() => navigate(-1)}
            sx={{ color: '#44403C', mr: 1 }}
          >
            <ArrowBackIos sx={{ fontSize: '1.25rem' }} />
          </IconButton>
          <Typography 
            variant="h1" 
            sx={{ 
              flex: 1,
              textAlign: 'center',
              fontSize: '1.25rem',
              fontWeight: 'bold',
              pr: 8,
              color: '#1C1917',
            }}
          >
            Settings
          </Typography>
        </Box>
      </Box>

      {/* Main Content */}
      <Box sx={{ px: 2, py: 3, display: 'flex', flexDirection: 'column', gap: 4 }}>
        {/* Account Section */}
        <Box>
          <Typography 
            variant="h2" 
            sx={{ 
              mb: 1.5,
              fontSize: '1.125rem',
              fontWeight: 600,
              color: '#1C1917',
            }}
          >
            Account
          </Typography>
          <Card>
            <CardContent sx={{ p: '16px !important' }}>
              <Box 
                onClick={() => setShowEditDialog(true)}
                sx={{ 
                  display: 'flex',
                  alignItems: 'center',
                  gap: 2,
                  cursor: 'pointer',
                  '&:hover': {
                    bgcolor: '#F5F5F4',
                  },
                }}
              >
                <Box 
                  sx={{ 
                    width: 56,
                    height: 56,
                    borderRadius: '50%',
                    bgcolor: '#FEE2E2',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                />
                <Box sx={{ flex: 1 }}>
                  <Typography sx={{ fontSize: '1rem', fontWeight: 500, color: '#1C1917' }}>
                    {userInfo?.name || 'Profile'}
                  </Typography>
                  <Typography sx={{ fontSize: '0.875rem', color: '#78716C' }}>
                    Edit your profile information
                  </Typography>
                </Box>
                <ChevronRight sx={{ color: '#A8A29E' }} />
              </Box>
            </CardContent>
          </Card>
        </Box>

        {/* Subscription Section */}
        <Box>
          <Typography 
            variant="h2" 
            sx={{ 
              mb: 1.5,
              fontSize: '1.125rem',
              fontWeight: 600,
              color: '#1C1917',
            }}
          >
            Subscription
          </Typography>
          <Card>
            <CardContent sx={{ p: '16px !important' }}>
              <Box sx={{ 
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                mb: 1,
              }}>
                <Typography sx={{ fontSize: '1rem', fontWeight: 500, color: '#1C1917' }}>
                  Current Plan
                </Typography>
                <Box sx={{ 
                  px: 2,
                  py: 0.5,
                  bgcolor: '#FEE2E2',
                  borderRadius: 'full',
                }}>
                  <Typography sx={{ 
                    fontSize: '0.875rem',
                    fontWeight: 600,
                    color: '#DC2626',
                  }}>
                    Free Trial
                  </Typography>
                </Box>
              </Box>
              <Typography sx={{ 
                fontSize: '0.875rem',
                color: '#78716C',
                mb: 2,
              }}>
                You are currently on the Free Trial plan. Upgrade to Premium for unlimited access to all features.
              </Typography>
              <Button
                variant="contained"
                fullWidth
                onClick={() => navigate('/subscription')}
                sx={{
                  bgcolor: '#DC2626',
                  color: 'white',
                  textTransform: 'none',
                  fontWeight: 'bold',
                  py: 3,
                  '&:hover': {
                    bgcolor: '#B91C1C',
                  },
                }}
              >
                Upgrade to Premium
              </Button>
            </CardContent>
          </Card>
        </Box>

        {/* Notification Section */}
        <Box>
          <Typography 
            variant="h2" 
            sx={{ 
              mb: 1.5,
              fontSize: '1.125rem',
              fontWeight: 600,
              color: '#1C1917',
            }}
          >
            Notification
          </Typography>
          <Card>
            <CardContent sx={{ p: '16px !important' }}>
              <Box sx={{ 
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
              }}>
                <Typography sx={{ fontSize: '1rem', color: '#1C1917' }}>
                  Allow push notifications
                </Typography>
                <Switch
                  checked={false}
                  onChange={() => {}}
                  sx={{
                    '& .MuiSwitch-switchBase.Mui-checked': {
                      color: '#DC2626',
                      '&:hover': {
                        backgroundColor: 'rgba(220, 38, 38, 0.04)',
                      },
                    },
                    '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
                      backgroundColor: '#DC2626',
                    },
                  }}
                />
              </Box>
            </CardContent>
          </Card>
        </Box>

        {/* Support Section */}
        <Box>
          <Typography 
            variant="h2" 
            sx={{ 
              mb: 1.5,
              fontSize: '1.125rem',
              fontWeight: 600,
              color: '#1C1917',
            }}
          >
            Support
          </Typography>
          <Card>
            <CardContent sx={{ p: '16px !important' }}>
              <Box 
                onClick={handleFeatureRequest}
                sx={{ 
                  display: 'flex',
                  alignItems: 'center',
                  cursor: 'pointer',
                  '&:hover': {
                    bgcolor: '#FAFAF9',
                  },
                }}
              >
                <Typography sx={{ flex: 1, fontSize: '1rem', color: '#1C1917' }}>
                  Request a Feature
                </Typography>
                <ChevronRight sx={{ color: '#A8A29E' }} />
              </Box>
            </CardContent>
          </Card>
        </Box>

        {/* Logout Section */}
        <Box sx={{ pt: 2 }}>
          <Button
            fullWidth
            variant="outlined"
            onClick={handleLogout}
            sx={{
              mb: 2,
              color: '#DC2626',
              borderColor: '#E7E5E4',
              bgcolor: 'white',
              textTransform: 'none',
              fontWeight: 'bold',
              height: '48px',
              '&:hover': {
                bgcolor: '#FAFAF9',
                borderColor: '#E7E5E4',
              },
            }}
          >
            Log Out
          </Button>
          <Button
            fullWidth
            variant="outlined"
            onClick={handleDeleteAllInformation}
            sx={{
              mb: 2,
              color: '#DC2626',
              borderColor: '#E7E5E4',
              bgcolor: 'white',
              textTransform: 'none',
              fontWeight: 'bold',
              height: '48px',
              '&:hover': {
                bgcolor: '#FAFAF9',
                borderColor: '#E7E5E4',
              },
            }}
          >
            Delete all information
          </Button>
        </Box>
      </Box>

      {/* Dialogs */}
      <Dialog open={showEditDialog} onClose={() => setShowEditDialog(false)}>
        <DialogTitle>Edit My Information</DialogTitle>
        <DialogContent>
          <Stack spacing={3} sx={{ mt: 3 }}>
            <TextField
              label="Name"
              fullWidth
              value={userInfo?.name || ''}
              onChange={(e) => setUserInfo(userInfo ? { ...userInfo, name: e.target.value } : null)}
            />
            <TextField
              label="Birth Date"
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              value={userInfo?.birthDate || ''}
              onChange={(e) => setUserInfo(userInfo ? { ...userInfo, birthDate: e.target.value } : null)}
            />
            <FormControlLabel
              control={
                <Switch
                  checked={userInfo?.isTimeUnknown || false}
                  onChange={(e) => setUserInfo(userInfo ? { ...userInfo, isTimeUnknown: e.target.checked } : null)}
                />
              }
              label="I don't know my birth time"
            />
            {!userInfo?.isTimeUnknown && (
              <TextField
                label="Birth Time"
                type="time"
                fullWidth
                InputLabelProps={{ shrink: true }}
                value={userInfo?.birthTime || ''}
                onChange={(e) => setUserInfo(userInfo ? { ...userInfo, birthTime: e.target.value } : null)}
              />
            )}
            <FormControl fullWidth>
              <InputLabel>Gender</InputLabel>
              <Select
                value={userInfo?.gender || ''}
                label="Gender"
                onChange={(e) => setUserInfo(userInfo ? { ...userInfo, gender: e.target.value as Gender } : null)}
              >
                <MenuItem value="Male">Male</MenuItem>
                <MenuItem value="Female">Female</MenuItem>
                <MenuItem value="Other">Other</MenuItem>
              </Select>
            </FormControl>
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowEditDialog(false)}>Cancel</Button>
          <Button 
            onClick={handleEditSubmit} 
            variant="contained"
            disabled={!userInfo}
          >
            Save
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={showLogoutDialog} onClose={() => setShowLogoutDialog(false)}>
        <DialogTitle>Confirm Logout</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure you want to logout?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowLogoutDialog(false)}>Cancel</Button>
          <Button onClick={handleLogout} color="error">Logout</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Settings; 