import React from 'react';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Stack,
  Button,
  Divider,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { SajuReading } from '../types/saju';

// Mock data
const mockSajuReading: SajuReading = {
  id: '1',
  userId: '1',
  mainElement: 'Fire',
  supportingElement: 'Wood',
  challengingElement: 'Water',
  elementalBalance: {
    fire: 40,
    earth: 20,
    metal: 15,
    water: 10,
    wood: 15,
  },
  summary: 'Your energy is primarily driven by the Fire element, supported by Wood. Be cautious of Water influences.',
  detailedReading: 'Detailed reading here...',
  createdAt: new Date().toISOString(),
  dailyFortune: {
    date: new Date().toISOString(),
    fortune: 'Today is a favorable day for new beginnings. Trust your intuition.',
    luckyColor: 'Red',
    luckyNumber: 7,
  },
};

const MySaju: React.FC = () => {
  const navigate = useNavigate();

  return (
    <Container 
      sx={{ 
        pb: '72px',
        pt: 2,
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column'
      }}
    >
      <Box sx={{ mb: 4 }}>
        <Typography variant="h1" gutterBottom>
          My Saju Profile
        </Typography>
        <Typography variant="body1" color="text.secondary">
          View your personal Saju reading and daily insights
        </Typography>
      </Box>

      <Stack spacing={3} sx={{ flex: 1 }}>
        <Card>
          <CardContent>
            <Typography variant="h2" gutterBottom>
              Your Elements
            </Typography>
            <Box sx={{ mb: 3 }}>
              <Typography variant="body1" paragraph>
                Main Element: <strong>{mockSajuReading.mainElement}</strong>
              </Typography>
              <Typography variant="body1" paragraph>
                Supporting Element: <strong>{mockSajuReading.supportingElement}</strong>
              </Typography>
              <Typography variant="body1">
                Challenging Element: <strong>{mockSajuReading.challengingElement}</strong>
              </Typography>
            </Box>
            <Divider sx={{ my: 2 }} />
            <Typography variant="h3" gutterBottom>
              Summary
            </Typography>
            <Typography variant="body1" color="text.secondary" paragraph>
              {mockSajuReading.summary}
            </Typography>
            <Button
              variant="outlined"
              color="primary"
              fullWidth
              onClick={() => navigate('/detailed-reading')}
            >
              View Detailed Reading
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardContent>
            <Typography variant="h2" gutterBottom>
              Daily Fortune
            </Typography>
            <Typography variant="body1" paragraph>
              {mockSajuReading.dailyFortune?.fortune}
            </Typography>
            <Box sx={{ display: 'flex', gap: 3 }}>
              <Box>
                <Typography variant="caption" color="text.secondary" display="block">
                  Lucky Color
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 500 }}>
                  {mockSajuReading.dailyFortune?.luckyColor}
                </Typography>
              </Box>
              <Box>
                <Typography variant="caption" color="text.secondary" display="block">
                  Lucky Number
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 500 }}>
                  {mockSajuReading.dailyFortune?.luckyNumber}
                </Typography>
              </Box>
            </Box>
          </CardContent>
        </Card>

        <Button
          variant="contained"
          color="primary"
          fullWidth
          onClick={() => navigate('/subscription')}
        >
          Upgrade to Premium
        </Button>
      </Stack>
    </Container>
  );
};

export default MySaju; 