import { createTheme } from '@mui/material/styles';

export const lightTheme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#dc2626',
      dark: '#b91c1c',
    },
    background: {
      default: '#F2F4F7',
      paper: '#FFFFFF',
    },
    text: {
      primary: '#292524'
    },
  },
  typography: {
    fontFamily: 'SF Pro Display, SF Pro, -apple-system, BlinkMacSystemFont, Roboto, sans-serif',
    h1: {
      fontSize: '24px',
      fontWeight: 700,
      color: '#EF4444',
    },
    h2: {
      fontSize: '20px',
      fontWeight: 600,
    },
    body1: {
      fontSize: '15px',
    },
    caption: {
      fontSize: '11px',
    },
  },
  shape: {
    borderRadius: 12,
  },
  spacing: 4,
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: '16px',
          backgroundColor: '#FFFFFF',
          boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.02), 0px 2px 4px rgba(0, 0, 0, 0.04)',
        },
      },
      defaultProps: {
        elevation: 0,
      },
    },
    MuiCardContent: {
      styleOverrides: {
        root: {
          padding: '18px',
          '&:last-child': {
            paddingBottom: '18px',
          },
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          padding: '16px 24px',
          borderRadius: 'lg',
          fontWeight: 700,
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            backgroundColor: '#FFFFFF',
            '& fieldset': {
              border: 'none',
            },
            '&:hover fieldset': {
              border: 'none',
            },
            '&.Mui-focused fieldset': {
              border: '2px solid #EF4444',
            },
          },
        },
      },
    },
    MuiSelect: {
      styleOverrides: {
        root: {
          backgroundColor: '#FFFFFF',
          '& .MuiOutlinedInput-notchedOutline': {
            border: 'none',
          },
          '&:hover .MuiOutlinedInput-notchedOutline': {
            border: 'none',
          },
          '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
            border: '2px solid #EF4444',
          },
        },
      },
    },
    MuiCheckbox: {
      styleOverrides: {
        root: {
          color: '#dfdce5',
          '&.Mui-checked': {
            color: '#EF4444',
          },
        },
      },
    },
  },
}); 