import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import {
  Box,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormControlLabel,
  Checkbox,
  Typography,
  Container,
  InputAdornment,
  IconButton,
  CircularProgress,
  Backdrop,
} from '@mui/material';
import { UserInfo, Gender } from '../types/user';
import PersonIcon from '@mui/icons-material/Person';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import WcIcon from '@mui/icons-material/Wc';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

const currentYear = new Date().getFullYear();

const validationSchema = Yup.object({
  name: Yup.string().required('Name is required'),
  birthDate: Yup.string()
    .required('Birth date is required')
    .matches(/^\d{4}-\d{2}-\d{2}$/, 'Date must be in YYYY-MM-DD format')
    .test('valid-year', 'Invalid year', (value) => {
      if (!value) return false;
      const year = parseInt(value.split('-')[0]);
      return year <= currentYear && year >= 1900;
    }),
  birthTime: Yup.string()
    .matches(/^([01]?[0-9]|2[0-3]):[0-5][0-9]$/, 'Time must be in HH:MM format')
    .when('isTimeUnknown', {
      is: false,
      then: (schema) => schema.required('Birth time is required'),
    }),
  gender: Yup.string().required('Gender is required'),
});

const Onboarding: React.FC = () => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);

  const handleBack = () => {
    navigate('/');
  };

  const formik = useFormik({
    initialValues: {
      name: '',
      birthDate: '',
      birthTime: '',
      isTimeUnknown: false,
      gender: '' as Gender,
    },
    validationSchema,
    onSubmit: async (values: UserInfo) => {
      setIsLoading(true);
      // Store user info in localStorage
      localStorage.setItem('userInfo', JSON.stringify(values));
      
      // Simulate loading time
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setIsLoading(false);
      navigate('/home');
    },
  });

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <Backdrop
        sx={{ 
          color: '#fff', 
          zIndex: (theme) => theme.zIndex.drawer + 1,
          display: 'flex',
          flexDirection: 'column',
          gap: 2
        }}
        open={isLoading}
      >
        <CircularProgress color="inherit" size={60} />
        <Typography variant="h6" color="inherit">
          Analyzing your Saju...
        </Typography>
      </Backdrop>
      <Container maxWidth="sm" sx={{ flex: 1, px: 6 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 4, py: 6 }}>
          <header>
            <Box sx={{ position: 'relative', mb: 4, pt: 1 }}>
              <IconButton
                onClick={handleBack}
                sx={{
                  position: 'absolute',
                  left: -12,
                  top: 0,
                }}
              >
                <ArrowBackIcon />
              </IconButton>
              <Typography 
                variant="h1" 
                align="center" 
                gutterBottom
                sx={{
                  fontSize: { xs: '1.75rem', sm: '2.125rem' },
                  ml: { xs: 4, sm: 0 },
                  mr: { xs: 4, sm: 0 },
                  lineHeight: 1.2
                }}
              >
                Welcome to Elemental Saju
              </Typography>
            </Box>
          </header>

          <form onSubmit={formik.handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
            <Box>
              <Typography variant="body1" sx={{ mb: 1, fontWeight: 500 }}>
                Name
              </Typography>
              <TextField
                fullWidth
                id="name"
                name="name"
                placeholder="Enter your name"
                value={formik.values.name}
                onChange={formik.handleChange}
                error={formik.touched.name && Boolean(formik.errors.name)}
                helperText={formik.touched.name && formik.errors.name}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonIcon sx={{ color: 'text.secondary' }} />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>

            <Box>
              <Typography variant="body1" sx={{ mb: 1, fontWeight: 500 }}>
                Date of Birth
              </Typography>
              <TextField
                fullWidth
                id="birthDate"
                name="birthDate"
                type="date"
                value={formik.values.birthDate}
                onChange={formik.handleChange}
                error={formik.touched.birthDate && Boolean(formik.errors.birthDate)}
                helperText={formik.touched.birthDate && formik.errors.birthDate}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <CalendarTodayIcon sx={{ color: 'text.secondary' }} />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>

            <Box>
              <Typography variant="body1" sx={{ mb: 1, fontWeight: 500 }}>
                Time of Birth
              </Typography>
              {!formik.values.isTimeUnknown && (
                <TextField
                  fullWidth
                  id="birthTime"
                  name="birthTime"
                  type="time"
                  value={formik.values.birthTime}
                  onChange={formik.handleChange}
                  error={formik.touched.birthTime && Boolean(formik.errors.birthTime)}
                  helperText={formik.touched.birthTime && formik.errors.birthTime}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccessTimeIcon sx={{ color: 'text.secondary' }} />
                      </InputAdornment>
                    ),
                  }}
                />
              )}
              <FormControlLabel
                control={
                  <Checkbox
                    checked={formik.values.isTimeUnknown}
                    onChange={formik.handleChange}
                    name="isTimeUnknown"
                  />
                }
                label="I don't know my time of birth"
                sx={{ mt: 2 }}
              />
            </Box>

            <Box>
              <Typography variant="body1" sx={{ mb: 1, fontWeight: 500 }}>
                Gender
              </Typography>
              <FormControl fullWidth>
                <Select
                  id="gender"
                  name="gender"
                  value={formik.values.gender}
                  onChange={formik.handleChange}
                  error={formik.touched.gender && Boolean(formik.errors.gender)}
                  displayEmpty
                  startAdornment={
                    <InputAdornment position="start">
                      <WcIcon sx={{ color: 'text.secondary' }} />
                    </InputAdornment>
                  }
                >
                  <MenuItem value="" disabled>
                    Select Gender
                  </MenuItem>
                  <MenuItem value="Male">Male</MenuItem>
                  <MenuItem value="Female">Female</MenuItem>
                  <MenuItem value="Other">Other</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </form>
        </Box>
      </Container>

      <Box sx={{ px: 6, py: 6, bgcolor: 'background.paper', boxShadow: '0px -1px 4px rgba(0, 0, 0, 0.1)' }}>
        <Container maxWidth="sm" disableGutters>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            size="large"
            fullWidth
            onClick={() => formik.handleSubmit()}
            disabled={isLoading}
          >
            {isLoading ? <CircularProgress size={24} color="inherit" /> : 'See My Saju'}
          </Button>
        </Container>
      </Box>
    </Box>
  );
};

export default Onboarding; 