import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { ThemeProvider, CssBaseline } from '@mui/material';
import { lightTheme } from './styles/theme';

// Pages
import Start from './pages/Start';
import Onboarding from './pages/Onboarding';
import Home from './pages/Home';
import DetailedSajuReading from './pages/DetailedSajuReading';
import Friends from './pages/Friends';
import Compatibility from './pages/Compatibility';
import Settings from './pages/Settings';
import Subscription from './pages/Subscription';
import MySaju from './pages/MySaju';

// Components
import BottomNav from './components/BottomNav';

// Bottom Navigation wrapper component
const BottomNavWrapper = () => {
  const location = useLocation();
  const hideBottomNavRoutes = ['/', '/onboarding'];
  
  if (hideBottomNavRoutes.includes(location.pathname)) {
    return null;
  }
  
  return <BottomNav />;
};

const App: React.FC = () => {
  useEffect(() => {
    // Function to set custom vh
    const setScreenSize = () => {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    };

    // Set initial size
    setScreenSize();

    // Add event listener for resize and orientation change
    window.addEventListener('resize', setScreenSize);
    window.addEventListener('orientationchange', setScreenSize);

    // Cleanup
    return () => {
      window.removeEventListener('resize', setScreenSize);
      window.removeEventListener('orientationchange', setScreenSize);
    };
  }, []);

  return (
    <ThemeProvider theme={lightTheme}>
      <CssBaseline />
      <Router>
        <Routes>
          <Route path="/" element={<Start />} />
          <Route path="/onboarding" element={<Onboarding />} />
          <Route path="/home" element={<Home />} />
          <Route path="/my-saju" element={<MySaju />} />
          <Route path="/detailed-reading" element={<DetailedSajuReading />} />
          <Route path="/friends" element={<Friends />} />
          <Route path="/compatibility/:friendId" element={<Compatibility />} />
          <Route path="/settings" element={<Settings />} />
          <Route path="/subscription" element={<Subscription />} />
        </Routes>
        <BottomNavWrapper />
      </Router>
    </ThemeProvider>
  );
};

export default App; 