const ElementalPentagon: React.FC<{ elementalBalance: ElementalBalance }> = ({
  elementalBalance,
}) => {
  const data = [
    { element: 'Fire', value: elementalBalance.fire, icon: <LocalFireDepartment sx={{ color: '#EF4444' }} />, color: '#EF4444' },
    { element: 'Earth', value: elementalBalance.earth, icon: <Public sx={{ color: '#92400E' }} />, color: '#92400E' },
    { element: 'Metal', value: elementalBalance.metal, icon: <Diamond sx={{ color: '#9CA3AF' }} />, color: '#9CA3AF' },
    { element: 'Water', value: elementalBalance.water, icon: <Opacity sx={{ color: '#3B82F6' }} />, color: '#3B82F6' },
    { element: 'Wood', value: elementalBalance.wood, icon: <Grass sx={{ color: '#22C55E' }} />, color: '#22C55E' },
  ];

  const size = 200;
  const center = size / 2;
  const radius = (size / 2) * 0.8;

  // Find the maximum value from user's elements
  const maxValue = Math.max(...data.map(item => item.value));
  // Round up to the nearest 10 for better grid lines
  const gridMax = Math.ceil(maxValue / 10) * 10;
  
  // Calculate pentagon points
  const getPoint = (index: number, value: number) => {
    const angle = (Math.PI * 2 * index) / 5 - Math.PI / 2;
    const scaledValue = (value / gridMax) * 100;
    const distance = (radius * scaledValue) / 100;
    return {
      x: center + distance * Math.cos(angle),
      y: center + distance * Math.sin(angle),
    };
  };

  // Generate points for the value pentagon
  const points = data.map((item, index) => getPoint(index, item.value));
  const pathData = points.map((point, index) => 
    (index === 0 ? 'M' : 'L') + `${point.x},${point.y}`
  ).join(' ') + 'Z';

  // Generate points for the background pentagon (100%)
  const bgPoints = Array(5).fill(0).map((_, index) => getPoint(index, gridMax));
  const bgPathData = bgPoints.map((point, index) => 
    (index === 0 ? 'M' : 'L') + `${point.x},${point.y}`
  ).join(' ') + 'Z';

  // Generate grid lines (4 levels)
  const gridLines = Array(4).fill(0).map((_, i) => {
    const level = gridMax * (i + 1) / 5;
    const gridPoints = Array(5).fill(0).map((_, index) => getPoint(index, level));
    return gridPoints.map((point, index) => 
      (index === 0 ? 'M' : 'L') + `${point.x},${point.y}`
    ).join(' ') + 'Z';
  });

  return (
    <Box sx={{ width: '100%', mt: 2 }}>
      <Box sx={{ display: 'flex', justifyContent: 'center', mb: 3 }}>
        <svg width={size} height={size} viewBox={`0 0 ${size} ${size}`}>
          {/* Background pentagon grid */}
          {gridLines.map((line, i) => (
            <path
              key={`grid-${i}`}
              d={line}
              fill="none"
              stroke="#E5E7EB"
              strokeWidth="1"
            />
          ))}
          
          {/* Background pentagon */}
          <path
            d={bgPathData}
            fill="none"
            stroke="#E5E7EB"
            strokeWidth="1"
          />
          
          {/* Value pentagon */}
          <path
            d={pathData}
            fill="#93C5FD"
            fillOpacity="0.3"
            stroke="#3B82F6"
            strokeWidth="2"
          />

          {/* Element labels */}
          {data.map((item, index) => {
            const labelPoint = getPoint(index, gridMax * 1.15);
            return (
              <text
                key={item.element}
                x={labelPoint.x}
                y={labelPoint.y}
                textAnchor="middle"
                dominantBaseline="middle"
                fill="#6B7280"
                fontSize="12"
                fontWeight="500"
              >
                {item.element}
              </text>
            );
          })}
        </svg>
      </Box>
      
      <Stack 
        direction="row" 
        spacing={3} 
        justifyContent="center"
      >
        {data.map((item) => (
          <Box 
            key={item.element} 
            sx={{ 
              display: 'flex', 
              alignItems: 'center',
              gap: 0.5,
            }}
          >
            {item.icon}
            <Typography variant="caption" sx={{ fontWeight: 500 }}>
              {item.value}%
            </Typography>
          </Box>
        ))}
      </Stack>
    </Box>
  );
};

