import React from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Container,
  Stack,
  Divider,
  IconButton,
  Button,
} from '@mui/material';
import { Share as ShareIcon } from '@mui/icons-material';
import { SajuReading } from '../types/saju';

// Mock data (나중에 실제 데이터로 교체)
const mockSajuReading: SajuReading = {
  id: '1',
  userId: '1',
  mainElement: 'Fire',
  supportingElement: 'Wood',
  challengingElement: 'Water',
  elementalBalance: {
    fire: 40,
    earth: 20,
    metal: 15,
    water: 10,
    wood: 15,
  },
  summary: 'Your energy is primarily driven by the Fire element, supported by Wood. Be cautious of Water influences.',
  detailedReading: `Your Saju reading reveals a strong Fire element dominance, which brings both opportunities and challenges.

Key Characteristics:
- Natural leadership abilities and charisma
- Creative and passionate approach to life
- Strong drive for success and recognition

Supportive Elements:
The Wood element in your chart feeds your Fire energy, enhancing your:
- Growth potential
- Strategic thinking
- Ability to plan and execute

Challenging Elements:
Water elements in your environment may create obstacles by:
- Dampening your enthusiasm
- Creating emotional turbulence
- Requiring more energy to overcome challenges

Recommendations:
1. Harness your Fire energy for creative projects
2. Balance activities with rest periods
3. Seek Wood element support during challenging times
4. Be mindful of Water element influences

Career Path:
Your elemental balance suggests success in:
- Leadership positions
- Creative industries
- Entrepreneurial ventures
- Public speaking roles

Relationships:
Your Fire nature harmonizes well with:
- Wood elements (growth and support)
- Earth elements (stability)
Be cautious with:
- Water elements (potential conflicts)
- Metal elements (need for adaptation)

Health Considerations:
- Pay attention to heart health
- Practice stress management
- Maintain regular exercise
- Balance rest and activity`,
  createdAt: new Date().toISOString(),
};

const DetailedSajuReading: React.FC = () => {
  const handleShare = () => {
    // TODO: Implement sharing functionality
    console.log('Share reading');
  };

  return (
    <Container 
      sx={{ 
        pb: '72px', // bottom navigation height (56px) + extra padding
        pt: 2,
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column'
      }}
    >
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h1" gutterBottom>
          Detailed Reading
        </Typography>
        <IconButton onClick={handleShare} color="primary" aria-label="share">
          <ShareIcon />
        </IconButton>
      </Box>

      <Stack spacing={3} sx={{ flex: 1 }}>
        <Card>
          <CardContent>
            <Typography variant="h2" gutterBottom>
              Summary
            </Typography>
            <Typography variant="body1" paragraph>
              {mockSajuReading.summary}
            </Typography>
            <Divider sx={{ my: 2 }} />
            <Typography variant="h2" gutterBottom>
              Detailed Analysis
            </Typography>
            <Typography
              variant="body1"
              component="div"
              sx={{ whiteSpace: 'pre-line' }}
            >
              {mockSajuReading.detailedReading}
            </Typography>
          </CardContent>
        </Card>

        <Box sx={{ display: 'flex', gap: 2 }}>
          <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={handleShare}
          >
            Share Reading
          </Button>
          <Button
            variant="outlined"
            color="primary"
            fullWidth
            onClick={() => window.print()}
          >
            Save as PDF
          </Button>
        </Box>
      </Stack>
    </Container>
  );
};

export default DetailedSajuReading; 