import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Button,
  Container,
  Typography,
  useTheme,
} from '@mui/material';
import mainImage from '../assets/elemental_saju_main_image.png';

const Start: React.FC = () => {
  const navigate = useNavigate();
  const theme = useTheme();

  useEffect(() => {
    const userInfo = localStorage.getItem('userInfo');
    if (userInfo) {
      navigate('/home');
    }
  }, [navigate]);

  const handleGetStarted = () => {
    navigate('/onboarding');
  };

  return (
    <Box 
      sx={{ 
        display: 'flex', 
        flexDirection: 'column',
        minHeight: '100vh',
        bgcolor: 'background.default',
        p: 2.5,
      }}
    >
      {/* Header - 언어 선택 버튼 (추후 구현) */}
      {/* <Box sx={{ 
        width: '100%', 
        display: 'flex', 
        justifyContent: 'flex-end',
        pt: 1.5,
        mb: 4
      }}>
        <Button
          sx={{
            minWidth: 'auto',
            bgcolor: 'white',
            color: 'text.primary',
            borderRadius: '32px',
            boxShadow: '0 1px 2px rgba(0, 0, 0, 0.05)',
            px: 2,
            py: 1,
            '&:hover': {
              bgcolor: 'white',
            }
          }}
        >
          <Box 
            component="span" 
            sx={{ 
              fontSize: '20px', 
              mr: 0.5,
              display: 'flex',
              alignItems: 'center'
            }}
            role="img" 
            aria-label="US flag"
          >
            🇺🇸
          </Box>
          <Typography sx={{ fontSize: '14px', fontWeight: 500 }}>EN</Typography>
        </Button>
      </Box> */}

      {/* Main Content */}
      <Container 
        maxWidth="sm" 
        sx={{ 
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          textAlign: 'center',
          px: 2.5,
          pt: 8,  // 상단 여백 증가
          pb: 12,
        }}
      >
        {/* Top Section - Image Only */}
        <Box sx={{ 
          width: '100%', 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center',
          height: '60vh',
          justifyContent: 'center'
        }}>
          <Box
            component="img"
            src={mainImage}
            alt="Elemental Saju"
            sx={{
              height: '100%',
              width: 'auto',
              maxWidth: '100%',
              objectFit: 'contain'
            }}
          />
        </Box>

        {/* Bottom Section - Title, Description, and Button */}
        <Box sx={{ 
          width: '100%', 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center',
        }}>
          <Typography 
            variant="h1" 
            sx={{ 
              fontSize: '30px',
              fontWeight: 700,
              color: '#111827',
              mt: 7,  // 5에서 7로 증가
              mb: 3,
            }}
          >
            Elemental Saju
          </Typography>
          <Typography 
            variant="body1" 
            sx={{ 
              fontSize: '18px',
              color: '#4B5563',
              mb: 6,  // 버튼과의 간격 증가
              maxWidth: '280px',
              lineHeight: 1.4
            }}
          >
            Find your Elemental Attribute and Saju-Destiny
          </Typography>
          <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={handleGetStarted}
            sx={{
              borderRadius: '9999px',
              py: 3,
              fontSize: '16px',
              fontWeight: 600,
            }}
          >
            Get Started
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default Start; 