import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { BottomNavigation, BottomNavigationAction, Paper } from '@mui/material';
import { Home, Person, People, Settings } from '@mui/icons-material';

const BottomNav: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();

  // Don't show bottom nav on onboarding screen
  if (location.pathname === '/onboarding') {
    return null;
  }

  return (
    <Paper
      sx={{
        position: 'fixed',
        bottom: 0,
        left: 0,
        right: 0,
        borderTop: '1px solid',
        borderColor: 'divider',
      }}
      elevation={3}
    >
      <BottomNavigation
        value={location.pathname}
        onChange={(_, newValue) => {
          navigate(newValue);
        }}
        showLabels
        sx={{
          '& .MuiBottomNavigationAction-root': {
            color: '#78716C',
            '&.Mui-selected': {
              color: '#DC2626',
            },
          },
          '& .MuiBottomNavigationAction-label': {
            fontSize: '0.625rem',
            '&.Mui-selected': {
              fontSize: '0.625rem',
            },
          },
        }}
      >
        <BottomNavigationAction
          label="Home"
          value="/home"
          icon={<Home />}
        />
        <BottomNavigationAction
          label="My Saju"
          value="/my-saju"
          icon={<Person />}
        />
        <BottomNavigationAction
          label="Friends"
          value="/friends"
          icon={<People />}
        />
        <BottomNavigationAction
          label="Settings"
          value="/settings"
          icon={<Settings />}
        />
      </BottomNavigation>
    </Paper>
  );
};

export default BottomNav; 