import React, { useState, useEffect } from 'react';
import * as Yup from 'yup';
import {
  Box,
  Container,
  Typography,
  TextField,
  ToggleButtonGroup,
  ToggleButton,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Button,
  Card,
  CardContent,
  Stack,
  Divider,
  IconButton,
  Badge,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControlLabel,
  Switch,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  DialogContentText,
} from '@mui/material';
import {
  Person as PersonIcon,
  Mail as MailIcon,
  Compare as CompareIcon,
  PersonAdd as PersonAddIcon,
  Add as AddIcon,
  ArrowBackIos,
  ChevronRight,
} from '@mui/icons-material';
import { Friend, FriendRequest, FriendSearchResult, FriendListFilters } from '../types/friend';
import { useNavigate } from 'react-router-dom';
import { UserInfo, Gender } from '../types/user';

interface FriendWithCompatibility extends UserInfo {
  compatibility: number;
}

const currentYear = new Date().getFullYear();
const validationSchema = Yup.object({
  name: Yup.string().required('Name is required'),
  birthDate: Yup.string()
    .required('Birth date is required')
    .matches(/^\d{4}-\d{2}-\d{2}$/, 'Date must be in YYYY-MM-DD format')
    .test('valid-year', 'Invalid year', (value) => {
      if (!value) return false;
      const year = parseInt(value.split('-')[0]);
      return year <= currentYear && year >= 1900;
    }),
  birthTime: Yup.string()
    .matches(/^([01]?[0-9]|2[0-3]):[0-5][0-9]$/, 'Time must be in HH:MM format')
    .when('isTimeUnknown', {
      is: false,
      then: (schema) => schema.required('Birth time is required'),
    }),
  gender: Yup.string().required('Gender is required'),
});

// Mock data
const mockFriends: Friend[] = [
  { id: '1', email: 'john@example.com', name: 'John Doe' },
  { id: '2', email: 'jane@example.com', name: 'Jane Smith' },
];

const mockPendingRequests: FriendRequest[] = [
  {
    id: '1',
    senderId: '3',
    receiverId: '1',
    status: 'pending',
    createdAt: new Date().toISOString(),
  },
];

const mockSearchResults: FriendSearchResult[] = [
  {
    id: '4',
    email: 'mike@example.com',
    name: 'Mike Johnson',
    isAlreadyFriend: false,
    hasPendingRequest: false,
  },
];

const Friends = () => {
  const [filters, setFilters] = useState<FriendListFilters>({
    searchTerm: '',
    searchBy: 'email',
  });

  const [showSearchResults, setShowSearchResults] = useState(false);

  const navigate = useNavigate();

  const [showAddDialog, setShowAddDialog] = useState(false);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [selectedFriendIndex, setSelectedFriendIndex] = useState<number | null>(null);
  const [friends, setFriends] = useState<FriendWithCompatibility[]>([]);
  const [showEmptyState, setShowEmptyState] = useState(true);
  const [newFriend, setNewFriend] = useState<UserInfo>({
    name: '',
    birthDate: '',
    birthTime: '',
    isTimeUnknown: false,
    gender: '' as Gender,
  });

  useEffect(() => {
    const storedFriends = localStorage.getItem('friends');
    if (storedFriends) {
      const parsedFriends = JSON.parse(storedFriends);
      // Add random compatibility scores for demo
      const friendsWithCompatibility = parsedFriends.map((friend: UserInfo) => ({
        ...friend,
        compatibility: Math.floor(Math.random() * 41) + 60, // Random number between 60-100
      }));
      setFriends(friendsWithCompatibility);
      setShowEmptyState(false);
    }
  }, []);

  const handleSearchByChange = (
    _: React.MouseEvent<HTMLElement>,
    newSearchBy: 'email' | 'name'
  ) => {
    if (newSearchBy !== null) {
      setFilters({ ...filters, searchBy: newSearchBy });
    }
  };

  const handleSearch = () => {
    if (filters.searchTerm) {
      setShowSearchResults(true);
      // TODO: Implement actual search
      console.log('Searching for:', filters);
    }
  };

  const handleAddFriend = () => {
    if (newFriend.name && newFriend.birthDate) {
      const newFriendWithCompatibility = {
        ...newFriend,
        compatibility: Math.floor(Math.random() * 41) + 60,
      };
      
      const updatedFriends = [...friends, newFriendWithCompatibility];
      setFriends(updatedFriends);
      localStorage.setItem('friends', JSON.stringify(updatedFriends));
      
      setNewFriend({
        name: '',
        birthDate: '',
        birthTime: '',
        isTimeUnknown: false,
        gender: 'Male',
      });
      setShowAddDialog(false);
      setShowEmptyState(false);
    }
  };

  const handleAcceptRequest = (requestId: string) => {
    // TODO: Implement request acceptance
    console.log('Accepting request:', requestId);
  };

  const handleRejectRequest = (requestId: string) => {
    // TODO: Implement request rejection
    console.log('Rejecting request:', requestId);
  };

  const handleCheckCompatibility = (friendId: number) => {
    navigate(`/compatibility/${friendId}`);
  };

  const handleLongPress = (index: number) => {
    setSelectedFriendIndex(index);
    setShowDeleteDialog(true);
  };

  const handleDeleteFriend = () => {
    if (selectedFriendIndex !== null) {
      const updatedFriends = friends.filter((_, index) => index !== selectedFriendIndex);
      setFriends(updatedFriends);
      localStorage.setItem('friends', JSON.stringify(updatedFriends));
      setShowDeleteDialog(false);
      setSelectedFriendIndex(null);
      
      if (updatedFriends.length === 0) {
        setShowEmptyState(true);
      }
    }
  };

  let longPressTimer: NodeJS.Timeout;
  const handleTouchStart = (index: number) => {
    longPressTimer = setTimeout(() => {
      handleLongPress(index);
    }, 500); // 500ms long press
  };

  const handleTouchEnd = () => {
    clearTimeout(longPressTimer);
  };

  return (
    <Box sx={{ 
      minHeight: '100vh',
      pb: '80px'
    }}>
      {/* Header */}
      <Box sx={{ 
        position: 'sticky',
        top: 0,
        zIndex: 10,
        bgcolor: 'white',
        boxShadow: '0 1px 2px rgba(0, 0, 0, 0.05)',
      }}>
        <Box sx={{ 
          display: 'flex',
          alignItems: 'center',
          p: 2,
        }}>
          <IconButton 
            onClick={() => navigate(-1)}
            sx={{ color: '#44403C', mr: 1 }}
          >
            <ArrowBackIos sx={{ fontSize: '1.25rem' }} />
          </IconButton>
          <Typography 
            variant="h1" 
            sx={{ 
              flex: 1,
              textAlign: 'center',
              fontSize: '1.25rem',
              fontWeight: 'bold',
              pr: 8,
              color: '#1C1917',
            }}
          >
            Friends
          </Typography>
          <IconButton
            onClick={() => setShowAddDialog(true)}
            sx={{
              color: '#1C1917',
              position: 'absolute',
              right: 16,
            }}
          >
            <AddIcon />
          </IconButton>
        </Box>
      </Box>

      {/* Main Content */}
      <Box sx={{ p: 2 }}>
        {/* Add Friend Button */}
        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'flex-end',
          mb: 2,
        }}>
          <Button
            onClick={() => setShowAddDialog(true)}
            sx={{
              color: '#DC2626',
              fontSize: '0.875rem',
              fontWeight: 500,
              '&:hover': {
                backgroundColor: 'transparent',
                color: '#B91C1C',
              },
            }}
          >
            +Check Saju with a Friend
          </Button>
        </Box>

        {/* Friend List */}
        {!showEmptyState && (
          <Stack spacing={2}>
            {friends.map((friend, index) => (
              <Card
                key={index}
                sx={{
                  p: 2,
                  borderRadius: '12px',
                  border: '1px solid #E5E7EB',
                  boxShadow: 'none',
                  '&:hover': {
                    borderColor: '#D1D5DB',
                  },
                  touchAction: 'none', // Prevents scrolling during long press
                }}
                onTouchStart={() => handleTouchStart(index)}
                onTouchEnd={handleTouchEnd}
                onMouseDown={() => handleTouchStart(index)}
                onMouseUp={handleTouchEnd}
                onMouseLeave={handleTouchEnd}
              >
                <Box sx={{ 
                  display: 'flex',
                  alignItems: 'center',
                  gap: 2,
                }}>
                  <Box
                    sx={{
                      width: 56,
                      height: 56,
                      borderRadius: '50%',
                      bgcolor: '#FEE2E2',
                      flexShrink: 0,
                    }}
                  />
                  <Box sx={{ flex: 1 }}>
                    <Typography sx={{ 
                      fontSize: '0.9375rem',
                      fontWeight: 500,
                      color: '#1C1917',
                    }}>
                      {friend.name}
                    </Typography>
                    <Typography sx={{ 
                      fontSize: '0.8125rem',
                      color: '#78716C',
                    }}>
                      Compatibility: <span style={{ fontWeight: 600, color: '#1C1917' }}>{friend.compatibility}%</span>
                    </Typography>
                  </Box>
                  <IconButton
                    onClick={() => handleCheckCompatibility(index)}
                    sx={{
                      color: '#1C1917',
                      '&:hover': {
                        bgcolor: '#F5F5F4',
                      },
                    }}
                  >
                    <ChevronRight />
                  </IconButton>
                </Box>
              </Card>
            ))}
          </Stack>
        )}

        {/* Empty State */}
        {showEmptyState && (
          <Box sx={{ 
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            textAlign: 'center',
            minHeight: 'calc(100vh - 64px - 80px)',
            px: 2,
          }}>
            <Box sx={{ 
              maxWidth: '320px',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              gap: 2,
            }}>
              {/* Empty State Icon */}
              <Box sx={{ 
                width: 120,
                height: 120,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: '#D1D5DB',
              }}>
                <PersonAddIcon sx={{ fontSize: 120, strokeWidth: 1 }} />
              </Box>

              <Typography 
                variant="h2" 
                sx={{ 
                  fontSize: '1.25rem',
                  fontWeight: 'bold',
                  color: '#1C1917',
                  mb: 1,
                }}
              >
                Check Saju with a Friend
              </Typography>

              <Typography 
                sx={{ 
                  fontSize: '0.9375rem',
                  color: '#78716C',
                  mb: 2,
                }}
              >
                Add friends to see your compatibility and explore your relationships through the lens of Saju.
              </Typography>

              <Button
                variant="contained"
                fullWidth
                onClick={() => setShowAddDialog(true)}
                sx={{
                  bgcolor: '#DC2626',
                  color: 'white',
                  textTransform: 'none',
                  fontWeight: 'bold',
                  py: 3,
                  '&:hover': {
                    bgcolor: '#B91C1C',
                  },
                }}
              >
                Try Now for Free!
              </Button>
            </Box>
          </Box>
        )}

        {/* Add Friend Dialog */}
        <Dialog open={showAddDialog} onClose={() => setShowAddDialog(false)}>
          <DialogTitle>Add Friend Information</DialogTitle>
          <DialogContent>
            <Stack spacing={3} sx={{ mt: 1 }}>
              <TextField
                label="Name"
                fullWidth
                value={newFriend.name}
                onChange={(e) => setNewFriend({ ...newFriend, name: e.target.value })}
              />
              <TextField
                label="Birth Date"
                type="date"
                fullWidth
                InputLabelProps={{ shrink: true }}
                value={newFriend.birthDate}
                onChange={(e) => setNewFriend({ ...newFriend, birthDate: e.target.value })}
              />
              <FormControlLabel
                control={
                  <Switch
                    checked={newFriend.isTimeUnknown}
                    onChange={(e) => setNewFriend({ ...newFriend, isTimeUnknown: e.target.checked })}
                  />
                }
                label="I don't know their birth time"
              />
              {!newFriend.isTimeUnknown && (
                <TextField
                  label="Birth Time"
                  type="time"
                  fullWidth
                  InputLabelProps={{ shrink: true }}
                  value={newFriend.birthTime}
                  onChange={(e) => setNewFriend({ ...newFriend, birthTime: e.target.value })}
                />
              )}
              <FormControl fullWidth>
                <InputLabel>Gender</InputLabel>
                <Select
                  value={newFriend.gender}
                  label="Gender"
                  onChange={(e) => setNewFriend({ ...newFriend, gender: e.target.value as Gender })}
                >
                  <MenuItem value="Male">Male</MenuItem>
                  <MenuItem value="Female">Female</MenuItem>
                  <MenuItem value="Other">Other</MenuItem>
                </Select>
              </FormControl>
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setShowAddDialog(false)}>Cancel</Button>
            <Button 
              onClick={handleAddFriend} 
              variant="contained"
              disabled={!newFriend.name || !newFriend.birthDate}
            >
              Add Friend
            </Button>
          </DialogActions>
        </Dialog>

        {/* Delete Confirmation Dialog */}
        <Dialog
          open={showDeleteDialog}
          onClose={() => setShowDeleteDialog(false)}
        >
          <DialogTitle>Delete Friend</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Are you sure you want to delete {selectedFriendIndex !== null ? friends[selectedFriendIndex]?.name : ''} from your friends list?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setShowDeleteDialog(false)}>
              Cancel
            </Button>
            <Button 
              onClick={handleDeleteFriend}
              color="error"
              variant="contained"
            >
              Delete
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </Box>
  );
};

export default Friends; 